@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Kumbh Sans', sans-serif;
}
body{
  background-color: #d8caca;
}
h1, h5{
  text-align: center;
  padding-top: 4rem;
  font-weight: 800;
  margin-bottom: 30px;
}
a{
  display: flex;
  justify-content: center;
  text-decoration: none;
  color: black;
}
i{
  color: yellow;
}
.heading{
  text-align: center;
}
.Navbar{
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  height: 3rem;
  align-items: center;
  margin-bottom: 15px;
  background: #5368df;
}
.Navbar li {
  list-style: none;
}
.Navbar li a{
  text-decoration: none;
  color: black;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 2px;
}
.Navbar li a:hover{
  border-bottom: 2px solid crimson;
}
.active{
  border-bottom: 2px solid crimson;

}

.movies{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  margin-top: 10px;
}
.card{
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  width: 22%;
  background: white;
  margin-right: 28px;
  margin-bottom: 30px;
  border-radius: 10px;
  box-shadow: 0 10px 10px 10px rgba(0, 0, 0,0.2);
}
.card h2{
  margin-left: 10px;
  font-size: 20px;
  margin-bottom: 5px;
  width: 90%;
  display: flex;
  justify-content: space-between;
}
.card h2 span{
  color: crimson;
  font-size: 18px;
  letter-spacing: 1px;
}
.movies .card img{
  width: 100%;
  height: 300px;
  cursor: pointer;
  margin-bottom: 10px;
}
.movies .card img:hover,
.movies .card img:focus{
  opacity: .8;
}
.buttons{
  display: flex;
  width: 90%;
  margin: auto;
  justify-content: space-between;
  text-align: center;
  align-items: center;
}
.buttons button{
  height: 40px;
  display: block;
}
.btn{
  padding: 5px 10px;
  width: 42%;
  height: 40px;
  font-size: 12px;
  margin-right: 20px;
  cursor: pointer;
  border-radius: 7px;
  background: #5368df;
  border: 2px solid #5368df;
  color: white;
  margin-bottom: 15px;
  text-align: center;
  display: flex;
  transition: 0.5s all ease-in-out;
  align-items: center;
}
.btn:hover{
  background: none;
  color: black;
}

.details{
  display: flex;
  flex-direction: row;
  width: 70%;
  justify-content: space-between;
  margin: auto;
}
.details a{
  width: 7rem;
  display: flex;
  justify-content: center;
}
.data{
  width: 50%;
}
.data h4, h3{
  margin-bottom: 15px;
}
.data p{
  font-size: 18px;
  margin-bottom: 5px;
}
.details img{
  width: 30%;
  height: 15rem;
}

@media only screen and (max-width:1200px) {
  .card{
    width: 40%;
  }
  .movies .card img{
    height: 260px;
  }
  .buttons{
    flex-direction: column;
    width: 90%;
  }
  .btn{
    width: 60%;
  }
  .details{
    flex-direction: column-reverse;
    width: 90%;
  }
  .data{
    width: 80%;
    margin: auto;
  }
  .details img{
    width: 50%;
    margin: auto;
    height: 15rem;
    margin-bottom: 20px;
  }
}
